.logo {
	height: 3.25rem;
	margin-left: 3rem;
}
.search {
	flex: 0 0 40%;

	display: flex;
	align-items: center;
	justify-content: center;

	@media only screen and (max-width: $bp-smallest) {
		order: 1;
		flex: 0 0 100%;
	}

	&__input {
		font-family: inherit;
		font-size: inherit;
		background-color: var(--color-grey-light-2);
		border: none;
		padding: .7rem 2rem;
		border-radius: 100px;
		width: 90%;
		transition: all .2s;
		margin-right: -3.5rem;

		&:focus {
			outline: none;
			width: 100%;
			background-color: var(--color-grey-light-3);
		}

		&::-webkit-input-placeholder {
			font-weight: 100;
			color: var(--color-grey-light-4);
		}
	}

	&__input:focus + &__button {
		background-color: var(--color-grey-light-3);
	}

	&__button {
		border: none;
		background-color: var(--color-grey-light-2);

		&:focus {
			outline: none;
		}

		&:active {
			transform: translateY(2px);
		}
	}

	&__icon {
		height: 2rem;
		width: 2rem;
		fill: var(--color-grey-dark-3);
	}
}

.user-nav {
	align-self: stretch;

	display: flex;
	align-items: center;

	& > * {
		padding: 0 2rem;
		cursor: pointer;
		height: 100%;
		display: flex;
		align-items: center;
	}

	& > *:hover {
		background-color: var(--color-grey-light-2);
	}

	&__icon-box {
		position: relative;
	}

	&__icon {
		height: 2.25rem;
		width: 2.25rem;
		fill: var(--color-grey-dark-2);
	}

	&__natification {
		font-size: .8rem;
		height: 1.75rem;
		width: 1.75rem;
		border-radius: 50%;
		background-color: var(--color-primary);
		color: #fff;
		position: absolute;
		top: 01.5rem;
		right: 1.1rem;

		display: flex;
		justify-content: center;
		align-items: center;
	}

	//&__user {	}

	&__user-photo {
		height: 3.75rem;
		border-radius: 50%;
		margin-right: 1rem;
	}

	//&__user-name {}
}

/////////////////////////////////////
.side-nav {
	font-size: 1.4rem;
	list-style: none;
	margin-top: 3.5rem;

	@media only screen and (max-width: $bp-medium) {
		display: flex;
		margin: 0;
	}

	&__item {
		position: relative;

		&:not(:last-child) {
			margin-bottom: .5rem;
			@media only screen and (max-width: $bp-medium) {
				margin: 0;
			}
		}

		@media only screen and (max-width: $bp-medium) {
			flex: 1;
		}
	}

	&__item::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 30px;
		background-color: var(--color-primary);
		transform: scaleY(0);
		transition: transform .2s, width .4s cubic-bezier(1, 0, 0, 1) .2s, background-color .1s;
	}

	&__item:hover::before,
	&__item-active::before {
		transform: scaleY(1);
		width: 100%;
	}

	&__item:active::before {
		background-color: var(--color-primary-light);
	}

	&__link:link,
	&__link:visited {
		color: var(--color-grey-light-1);
		text-decoration: none;
		text-transform: uppercase;
		display: block;
		padding: 1.5rem 3rem;
		position: relative;
		z-index: 10;

		display: flex;
		align-items: center;

		@media only screen and (max-width: $bp-medium) {
			justify-content: center;
			padding: 2rem;
		}

		@media only screen and (max-width: $bp-small) {
			flex-direction: column;
			padding: 1.5rem .5rem;
		}
	}

	&__icon {
		width: 1.75rem;
		height: 1.75rem;
		margin-right: 2rem;
		fill: currentColor;

		@media only screen and (max-width: $bp-small) {
			margin-right: 0;
			margin-bottom: .7rem;
			width: 1.5rem;
			height: 1.5rem;
		}
	}
}

//////////////////////////////////
.legal {
	color: var(--color-grey-light-4);
	font-size: 1.2rem;
	text-align: center;
	padding: 2.5rem;

	@media only screen and (max-width: $bp-medium) {
		display: none;
	}
}

///////////////////////////////////
.gallery {
	display: flex;
	&__photo {
		width: 100%;
		display: block;
	}
}

///////////////////
.overview {
	display: flex;
	align-items: center;

	border-bottom: 1px solid var(--color-grey-light-2);

	&__heading {
		font-size: 2.25rem;
		font-weight: 300;
		text-transform: uppercase;
		letter-spacing: 1rem;
		padding: 1.5rem 3rem;

		@media only screen and (max-width: $bp-small) {
			font-size: 1.8rem;
			padding: 1.25rem 2rem;
		}
	}

	&__stars {
		//flex: 1;
		margin-right: auto;
		display: flex;
	}

	&__icon-star,
	&__icon-location {
		width: 1.75rem;
		height: 1.75rem;
		fill: var(--color-primary);
	}

	&__location {
		font-size: 1.2rem;
		display: flex;
		vertical-align: center;
	}

	&__icon-location {
		margin-right: .5rem;
	}

	&__rating {
		background-color: var(--color-primary);
		margin-left: 3rem;
		color: white;
		padding: 0 2.25rem;
		align-self: stretch;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		@media only screen and (max-width: $bp-small) {
			padding: 0 1.5rem;
		}
	}

	&__rating-average {
		font-size: 2.25rem;
		font-weight: 300;
		margin-bottom: -3px;

		@media only screen and (max-width: $bp-small) {
			font-size: 1.5rem;
		}
	}

	&__rating-count {
		font-size: .8rem;
		text-transform: uppercase;

		@media only screen and (max-width: $bp-small) {
			font-size: .5rem;
		}
	}
}

//////////////////////
.btn-inline {
	border: none;
	color: var(--color-primary);
	font-size: inherit;
	border-bottom: 1px solid currentColor;
	padding-bottom: 2px;
	display: inline-block;
	background-color: transparent;
	cursor: pointer;
	transition: all .2s;

	& span {
		margin-left: 3px;
		transition: margin-left .2s;
	}

	&:hover {
		color: var(--color-grey-dark-1);

		span {
			margin-left: 8px;
		}
	}

	&:focus {
		outline: none;
		animation: pulaste 1s infinite;
	}
}

@keyframes pulaste {
	0% {
		transform: scale(1);
		box-shadow: none;
	}

	50% {
		transform: scale(1.05);
		box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
	}

	100% {
		transform: scale(1);
		box-shadow: none;
	}
}

//////////////////////////////////

.paragraph:not(:last-child) {
	margin-bottom: 2rem;
}

///////////////////////////////////
.list {
	list-style: none;
	margin: 3rem 0;
	padding: 3rem 0;
	border-top: 1px solid wheat;
	border-bottom: 1px solid wheat;

	display: flex;
	flex-wrap: wrap;

	&__item {
		flex: 0 0 50%;
		margin-bottom: .7rem;
	}

	&__item::before {
		content: "";
		display: inline-block;
		height: 1rem;
		width: 1rem;
		margin-right: .7rem;

		//background-image: url(images/chevron-small-right.svg);
		//background-size: cover;

		background-color: var(--color-primary);
		-webkit-mask-image: url(images/chevron-small-right.svg);
		-webkit-mask-size: cover;
		mask-image: url(images/chevron-small-right.svg);
		mask-size: cover;
	}
}

//////////////////////////////////
.recomend {
	font-size: 1.3rem;
	color: var(--color-grey-darkt-3);

	display: flex;
	align-items: center;

	&__conut {
		margin-right: auto;
	}

	&__friends {
		display: flex;
	}

	&__photo {
		box-sizing: content-box;
		height: 4rem;
		width: 4rem;
		border-radius: 50%;
		border: 3px solid white;

		&:not(:last-child) {
			margin-right: -2rem;
		}
	}
}

///////////////////////
.review {
	background-color: #fff;
	box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
	padding: 3rem;
	margin-bottom: 3.5rem;
	position: relative;

	@media only screen and (max-width: $bp-medium) {
		padding: 2rem;
		margin-bottom: 3rem;
	}

	&__text {
		margin-bottom: 1rem;
		z-index: 10;
		position: relative;
		overflow: hidden;
	}

	&__user {
		display: flex;
		align-items: center;
	}

	&__photo {
		height: 4.5rem;
		width: 4.5rem;
		border-radius: 50%;
		margin-right: 1.5rem;
	}

	&__user-box {
		margin-right: auto;
	}

	&__user-name {
		font-size: 1.1rem;
		font-weight: 600;
		text-transform: uppercase;
		margin-bottom: .4rem;
	}

	&__user-date {
		font-size: 1rem;
		color: var(--color-grey-darkt-3);
	}

	&__rating {
		color: var(--color-primary);
		font-size: 2.2rem;
		font-weight: 300;
	}

	&::before {
		content: "\201C";
		position: absolute;
		top: -2.7rem;
		left: -1rem;
		line-height: 1;
		font-size: 20rem;
		color: var(--color-grey-light-2);
		font-family: sans-serif;
		z-index: 1;
	}
}

//////////////////////////////////

.cta {
	padding: 3.5rem;
	text-align: center;

	@media only screen and (max-width: $bp-medium) {
		padding: 2.5rem 0;
	}

	&__book-now {
		font-size: 2rem;
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 2.5rem;
	}
}

/////////////////////////////
.btn {
	font-size: 1.5rem;
	font-weight: 300;
	text-transform: uppercase;
	border-radius: 100px;
	border: none;
	background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
	color: white;
	position: relative;
	overflow: hidden;
	cursor: pointer;
	margin-top: 2rem;

	&__visible {
		display: inline-block;
		padding: 2rem 7.5rem;
		height: 100%;
		width: 100%;
	}

	& > * {
		display: inline-block;
		height: 100%;
		width: 100%;
		transition: all .2s;
	}

	&__invisible {
		position: absolute;
		padding: 2rem;
		left: 0;
		top: -100%;
	}

	&:hover {
		background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
	}

	&:hover &__visible {
		transform: translateY(100%);
	}

	&:hover &__invisible {
		top: 0;
	}

	&:focus {
		outline: none;
		animation: pulsate 1s infinite;
	}
}
////////////////////////////
